.page {
  position: relative;
  margin: 0 auto;
  max-width: 1300px;
  padding: 30px 0;

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-size: 40px;
  }

  &__section {
    margin-bottom: 100px;

    @include onMobile() {
      margin-bottom: 50px;
    }
  }

  &__description {
    padding-left: 7px;
    border-left: 2px solid $c-gray;
    overflow-x: hidden;
    height: max-content;
  }
}